.educator-section {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7rem 2rem;
}

.educator-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
}

.educator-image-container {
  flex: 1;
}

.educator-shape-container {
  flex: 1;
  display: flex;
  padding-right: 100px;
}

.educator-image {
  width: 546px;
  height: 352px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.educator-image:hover {
  transform: translateY(-10px);
}

.educator-shape {
  width: 228px;
  height: 349.5px;
  border-radius: 0px;
  margin-left: 50px;

}

.educator-text-container {
  flex: 1;
  text-align: right;
  width: 320px;
}

.educator-label {
  color: var(--primary-orange);
  font-weight: bold;
  margin-bottom: 1rem;
  font-size: 24px;
  font-family: geologica;
}

.educator-heading {
  font-size: 50px;
  color: var(--primary-grey);
  margin-bottom: 1rem;
  margin-left: -300px;
  font-family: lilita one;
}

.highlight-text {
  color: var(--primary-orange);
  margin-left: -300px;
  font-size: 50px;
  font-family: lilita one;
}

.educator-description {
  font-size: 20px;
  color: var(--primary-grey);
  margin-bottom: 1rem;
  line-height: 1.5;
  font-family: geologica;
}

.educator-button {
  display: inline-block;
  padding: 10px 20px;
  color: var(--primary-orange);
  border: 2px solid var(--primary-orange);
  border-radius: 8px;
  text-decoration: none;
  font-size: 19.19px;
  font-family: geologica;
  transition: all 0.3s ease;
}

.educator-button:hover {
  background-color: #ff7c2e;
  color: #fff;
}

@media(max-width:1024px) {
  .educator-image {
    width: 457px;
    height: 319px;
  }

  .educator-shape {
    width: 182px;
    height: 345.5px;
    border-radius: 0px;
    margin-left: 22px;
  }

  .educator-shape-container {
    padding-right: 0;
  }
}

@media (max-width: 768px) {

  .educator-content {
    flex-direction: row;
    align-items: center;
    text-align: center;
  }

  .educator-image {
    width: 310px;
    height: auto;
    margin-top: 20px;
  }

  .educator-shape-container {
    padding-right: 0;
    margin-top: 20px;
  }

  .educator-shape {
    width: 140px;
    height: 230.5px;
    border-radius: 0px;
    margin-left: 7px;
    margin-top: 8px;
  }

  .educator-text-container {
    flex: 1;
    text-align: right;
    width: 225px;
  }

  .educator-heading,
  .highlight-text {
    font-size: 35px;
    margin-left: -46px;
  }

  .educator-description {
    font-size: 14px;
    margin-top: -12px;
    margin-bottom: -13px;
  }

  .educator-button {
    font-size: 14px;
  }
}

