/* AboutUs.css */

.about-us-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* padding: 50px 10%; */
  background-color: var(--bg-color);

}

.about-us-info-text {
  margin-bottom: -50px;
}

.about-us-info-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
}

.about-us-info-text h2 {
  text-align: center;
  font-size: 24px;
  font-family: geologica;
  font-weight: 400;
  color: var(--primary-orange);
  margin-bottom: 10px;
}

.about-us-info-text h3 {
  color: var(--primary-orange);
  margin-top: -19px;
  text-align: center;
  font-size: 50px;
  color: var(--primary-grey);
  font-weight: 800;
  margin-bottom: -19px;
  font-family: lilita one;
}

.about-us-info-text p {
  font-size: 22px;
  margin-top: 20px;
  font-family: geologica;
  font-weight: 100;
  max-width: 920px;
  text-align: center;
  color: var(--primary-grey);
  /* padding-right: 18%;
 padding-left: 18%; */
}

.about-us-info-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 150px;
  padding: 76px;
}

/* Image Container */
.info-image-container {
  position: relative;
  width: 350px;
  height: 250px;
  margin: 0 auto;
  top: -64px;
  left: 70px
}

.info-bg-image {
  width: 404px;
  height: 367px;
  border-radius: 10px;
  /* background-color: rgba(255, 252, 252, 0.2);  */
  position: absolute;
  z-index: 1;
  left: -140px;
  top: -14px;
}

.info-dots-image {
  width: 109px;
  height: 455px;
  border-radius: 1px;
  position: absolute;
  z-index: 2;
  left: -100px;
  top: 1px;
}

.info-main-image {
  width: 469px;
  height: 309px;
  border-radius: 1px;
  position: absolute;
  z-index: 3;
  left: 20px;
  top: 3px;
}

.info-main-image img {
  width: 469px;
  height: 306px;
}

.info-cards {
  position: absolute;
  bottom: -181px;
  left: 195px;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  z-index: 4;
}

.info-card {
  width: 196px;
  height: 171px;
  background: #ffffffbf;

  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: relative;
  text-align: centre;
  border: 2px solid hsl(0deg 12.48% 95.32%);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.info-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  border: 2px solid hsl(0deg 0% 85%);
}

.info-card img {
  width: 40px;
  height: 40px;
  margin-bottom: -10px;
}

.info-card h3 {
  font-size: 12px;
  color: #333;
  font-family: "koho";
  font-weight: 800;

}

.info-card p {
  margin-top: -15px;
  font-size: 12px;
  color: #1e1e1e;
  font-family: koho;
  font-weight: 200;
  text-align: left;
  margin-left: 8px;
  padding: 4px;
}



/* Content Section */
.info-content {
  width: 39%;
  text-align: left;
  padding: 10px;
  margin-right: 39px;
}

.info-content h2 {
  font-size: 30px;
  font-weight: 800;
  font-family: lilita one;
  color: var(--highlight-color);
  margin-bottom: 20px;

}

.info-content h3 {
  font-size: 24px;
  font-weight: 700;
  color: var(--highlight-color);
  margin-bottom: 20px;
  font-family: geologica;
  padding-right: 120px;
}

.info-content p {
  font-size: 22px;
  font-weight: 100;
  font-family: geologica;
  color: var(--highlight-color);
  line-height: 1.6;
  margin-bottom: 20px;
  padding-right: 120px;
}

.info-know-more {
  display: inline-block;
  padding: 10px 26px;
  color: var(--primary-orange);
  border: 2px solid var(--primary-orange);
  border-radius: 8px;
  text-decoration: none;
  font-family: KoHo;
  font-size: 19.19px;
  transition: all 0.3s ease;
}

.info-know-more:hover {
  background-color: var(--primary-orange);
  color: #f5f5f5;
}

@media(max-width:1280px) {
  .about-us-info-section {
    gap: 210px;
  }
}

